.app__about {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  .app__about-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  
    margin: 3rem 0 2rem;
  
    .app__about-filter-item {
      padding: 0.5rem 1rem;
      border-radius: 0.5rem;
      font-weight: 800;
      cursor: pointer;
      transition: all 0.3s ease;
      margin: 0.5rem;
  
      &:hover {
        background-color: var(--secondary-color);
        color: #fff;
      }
  
      @media screen and (min-width: 2000px) {
        padding: 1rem 2rem;
        border-radius: 0.85rem;
      }
    }
  
    .item-active {
      background-color: var(--secondary-color);
      color: #fff;
    }
  }

  .app__about-container {
    width: 100%;
    padding: 40px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    @media screen and (max-width: 900px) {
      gap: 40px 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }

  .app__about-experience {

    .app__about-experience-item {
      border-radius: 5px;
      padding: 18px;
      border: 1px solid #ccc;
    }
  }

  @media screen and (max-width: 900px) {
    width: 80%;
  }

  @media screen and (max-width: 600px) {
    width: 100%;
  }
}