.app__works {
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;

    margin: 3rem 0 2rem;

    .app__work-filter-item {
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover {
            background-color: var(--secondary-color);
            color: #fff;
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;
        }

        @media screen and (max-width: 600px) {
            margin: 0 0.5rem 0 0;
        }
    }
    
    .item-active {
        background-color: var(--secondary-color);
        color: #fff;
    }
}

.app__work-portfolio {
    display: grid;
    padding: 0 40px;
    grid-template-columns: repeat(3, 1fr);

    .app__work-item {
        // width: 350px;
        flex-direction: column;

        margin: 1.5rem;
        padding: 20px;
        border-radius: 10px;

        cursor: pointer;
        transform: all 0.3s ease;

        &:hover {
            box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
        }

        @media screen and (min-width: 2000px) {
            padding: 1.25rem;
            border-radius: .75rem;
        }

        @media screen and (max-width: 600px) {
            margin: 1.5rem .5rem;
        }

        .app__work-item-tech {
            height: 32px;
            filter: grayscale(100%);

            &:hover {
                filter: grayscale(0%);
                cursor: pointer;
            }
        }

        @media screen and (max-width: 900px) {
            margin: 1rem;
        }
    }

    @media screen and (min-width: 2000px) {
        grid-template-columns: repeat(4, 1fr);
    }
    
    @media screen and (max-width: 900px) {
        grid-template-columns: repeat(2, 1fr);
        padding: 0;
    }
    
    @media screen and (max-width: 600px) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.app__work-img {
    width: 100%;
    height: 200px;

    position: relative;

    img {
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        height: 350px;
    }
}

.app__work-content {
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h3 {
        margin-top: .5rem;
        font-size: 1.2rem;
        line-height: 1.5;
    }
}

.app__work-item-icons {
    width: 60%;

    div {
        width: 45px;
        height: 45px;
        border-radius: 50%;
        margin-top: 1rem;

        display: flex;
        justify-content: center;
        align-items: center;

        transition: all 0.3s ease-in-out;

        svg {
            width: 22px;
            height: 22px;
            color: var(--gray-color);
        }

        &:hover {
            background-color: var(--secondary-color);
            border-color: var(--secondary-color);

            svg {
                color: var(--white-color);
            }
        }
    }
}